import React, { useState } from 'react'
import { Input, Modal } from 'zarm'
import Picker from '../../components/picker'
import CheckCode from '../../components/check-code'
import { Fetch } from '../../utils/request'
import s from './index.less'



const QUES = [
    {
        label: '',
        value: 0
    },
    {
        label: '',
        value: 1
    },
    {
        label: '',
        value: 2
    },
    {
        label: '',
        value: 3
    }
]

export default function(){
    const [ data, setData ] = useState({})


    function onChangePhoneInfo(val){
        onChangeItemVal('phoneInfo', val)
    }
    function onChangeItemVal(name, val){
        setData(Object.assign({},data,{
            [name]:val
        }))
    }


    function alert(msg){
        return Modal.alert({
            content: msg || '',
        });
    }

    function onSubmit(){
        let submitData = {}
        if(!data.name){
            return alert('')
        }
        submitData.name = data.name
        if(!data.phoneInfo){
            return alert('')
        }else{
            const { value, code, inputCode} = data.phoneInfo
            if(!value){
                return alert('')
            }
            if(!code){
                return alert('')
            }
            if(!inputCode){
                return alert('')
            }
            if(inputCode !== code){
                return alert('')
            }
        }
        submitData.phone = data.phoneInfo.value
        if(!data.que){
            return alert('')
        }
        submitData.question = data.que.label
        // console.log('submitData :', submitData);
        Fetch('addQue', {
            data: submitData
        }).then((resp) => {
            console.log('resp :', resp);
            if(resp.success){
                if(resp.data && resp.data.length){
                    Modal.alert({
                        title:'',
                        content:''
                    })
                }else{
                    Modal.alert({
                        title:'',
                        content:''
                    })
                }
            }
           
        })
    }

    return <div className={s.container}>
        <div className={s.top}>
            <div style={{marginBottom: '8px'}}>
                
            </div>
            <div>
                
            </div>
        </div>
        <div className={s.body}>
            <div className={s.formItem}>
                <div className={s.formItemLabel}>
                
                </div>
                <div className={s.formItemContent}>
                    <Input value={data.name} onChange={(val)=>{
                        onChangeItemVal('name', val)
                    }} style={{marginTop: '8px'}} placeholder=""></Input>
                </div>
            </div>
            <div className={s.formItem}>
                <div className={s.formItemLabel}>
                
                </div>
                <CheckCode value={data.phoneInfo} className={s.formItemContent} onChange={onChangePhoneInfo}></CheckCode>
                {/* <div className={s.formItemContent}>
                    <Input placeholder=""></Input>
                    <Button size="sm" style={{width: '120px'}}></Button>
                </div> */}
            </div>
            <div className={s.formItem}>
                <div className={s.formItemLabel}>
                    
                </div>
                <div className={s.formItemContent}>
                    <Picker dataSource={QUES} visible placeholder="" value={data.que} onChange={(val)=>{
                        onChangeItemVal('que', val)
                    }}>

                    </Picker>
                </div>
            </div>
            <div className={s.formItem}>
                <div className={s.formItemLabel}>
                    
                </div>
                <div className={s.formItemContent}>

                </div>
            </div>
        </div>
        <div style={{position:'fixed', bottom: 0, left: 0, right: 0}}>
            <div className="primary-btn" onClick={onSubmit}>
                
            </div>
        </div>
    </div>
}


